@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap');

* {
    margin: 0;
    padding: 0;
}

.header {
    background-color: #25ddde;
}

.container {
    margin: 0 auto;
    max-width: 1230px;
    padding: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-title {
    font-family: Montserrat, sans-serif;
    font-size: 25px;
    color: gray;
}

.header__login a {
    font-family: Montserrat, sans-serif;
    font-size: 25px;
    color: gray;
    text-decoration: none;
    float:right;
}

.header__logo {
    width: 25px;
    height: 25px;
    padding-top: 4px;
    padding-left: 3px;
}

.header__login__container{
    display:flex;
}