.navigation{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.navigation__item{
    text-decoration: none;
    padding: 10px 25px;
    color: #6c958b;
    font-size: 15px;
}
.navigation__item:hover{
    background-color: rgb(205, 205, 205);
    cursor: pointer ;
    transition: background-color .3s linear;
}