@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.appback{
  background-color: #dfdfdf;
}

.fl-row{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.fl-row-j{
  display: flex;
  flex-direction: row;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navigation{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.navigation__item{
    text-decoration: none;
    padding: 10px 25px;
    color: #6c958b;
    font-size: 15px;
}
.navigation__item:hover{
    background-color: rgb(205, 205, 205);
    cursor: pointer ;
    transition: background-color .3s linear;
}
.mypage {
    background-color: white;
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding: 15px;
}

.photo {
    width: 150px;
    height: 150px;
    border: 2px solid black;
}

.info {
    padding-left: 30px;
}

.status {
    padding-top: 10px;
    font-size: 18px;
    color: #000000;
    line-height: 1.3;
}

.username {
    font-size: 23px;
    color: #282c34;
    line-height: 1.3;
}


.addpost {
    font-size: 18px;
    padding: 0 5px;
    border: 1px solid rgba(143, 143, 143, 0.8);
    margin: 5px 0;
    width: 91%;
}

.addbutton {
    border: 1px solid rgba(143, 143, 143, 0.8);
    margin: 5px;
    font-family: Monserat, sans-serif;
    font-size: 15px;
    padding: 7px;
    background-color: #25ddde;
}

.border{
    border: 1px black solid;
}

.addbutton:hover{
    background-color: rgba(66,83,218,0.32);
    cursor: pointer;
    transition: background-color .1s;
}

.userpost {
    margin-top: 5px;
    border-bottom: 3px solid grey;
}

.usersposts__picture {
    width: 50px;
    height: 50px;
    border: 1px solid black;
    border-radius: 50%;
}

.posts__text, .likescount {
    font-family: Monserat, sans-serif;
    font-size: 20px;
    color: #282c34;
}

.posts__text {
    margin-top: 10px;
    padding-left: 10px;
}

.likescount {
    margin-top: 10px;
}

.form-control.error input,
.form-control.error textarea {
    border: 1px solid red;
}
* {
    margin: 0;
    padding: 0;
}

.header {
    background-color: #25ddde;
}

.container {
    margin: 0 auto;
    max-width: 1230px;
    padding: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-title {
    font-family: Montserrat, sans-serif;
    font-size: 25px;
    color: gray;
}

.header__login a {
    font-family: Montserrat, sans-serif;
    font-size: 25px;
    color: gray;
    text-decoration: none;
    float:right;
}

.header__logo {
    width: 25px;
    height: 25px;
    padding-top: 4px;
    padding-left: 3px;
}

.header__login__container{
    display:flex;
}
