.mypage {
    background-color: white;
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding: 15px;
}

.photo {
    width: 150px;
    height: 150px;
    border: 2px solid black;
}

.info {
    padding-left: 30px;
}

.status {
    padding-top: 10px;
    font-size: 18px;
    color: #000000;
    line-height: 1.3;
}

.username {
    font-size: 23px;
    color: #282c34;
    line-height: 1.3;
}


.addpost {
    font-size: 18px;
    padding: 0 5px;
    border: 1px solid rgba(143, 143, 143, 0.8);
    margin: 5px 0;
    width: 91%;
}

.addbutton {
    border: 1px solid rgba(143, 143, 143, 0.8);
    margin: 5px;
    font-family: Monserat, sans-serif;
    font-size: 15px;
    padding: 7px;
    background-color: #25ddde;
}

.border{
    border: 1px black solid;
}

.addbutton:hover{
    background-color: rgba(66,83,218,0.32);
    cursor: pointer;
    transition: background-color .1s;
}

.userpost {
    margin-top: 5px;
    border-bottom: 3px solid grey;
}

.usersposts__picture {
    width: 50px;
    height: 50px;
    border: 1px solid black;
    border-radius: 50%;
}

.posts__text, .likescount {
    font-family: Monserat, sans-serif;
    font-size: 20px;
    color: #282c34;
}

.posts__text {
    margin-top: 10px;
    padding-left: 10px;
}

.likescount {
    margin-top: 10px;
}
